.cardContainer {
  height: 147px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 20px 4px;
  border-bottom: solid 1px #e6e6e6;
  width: 347px;
  margin-left: 14px;
  margin-right: 14px;

}

.titleTop {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.textTitle {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.cardTitle {
  font-size: 14px;
  font-weight: 500;
}

.cardSubTitle {
  font-size: 12px;
  font-weight: 500;
}

.img {
  width: 80px;
  height: 80px;
  background-color: brown;
  border-radius: 6px;
}

.bedgeGroup {
  display: flex;
  flex-direction: row;
  gap: 7px;
}

.cBedge {
  width: 39px;
  height: 20px;
  padding: 4px 10px;
  background-color: #f2f2f2;
  font-size: 10px;
  border-radius: 24px;
}

.subWrapper {
  display: flex;
  flex-direction: row;
  gap: 12px;
}

.sub {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 4px;
}

.num {
  font-size: 10px;
  font-weight: 600;
}
.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 12px 14px;
}
