.notificationContainer {
    padding-left: 0;
    padding-right: 0;
}

/* 헤더 (버튼 영역) */
.notificationHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
    padding-bottom: 10px;
}

/* 버튼 기본 스타일 */
.notificationButton {
    border: none;
    padding: 8px 12px;
    background-color: transparent; /* 배경 투명 처리 */

    display: flex;
    align-items: center; /* 세로 중앙 정렬 */
    gap: 4px; /* 아이콘과 텍스트 사이 간격 (선택 사항) */
}

/* 전체 읽음처리 버튼 스타일 */
.readAllButton {
    color: var(--primary-30, #39A2A5);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

/* 전체 삭제 버튼 스타일 */
.deleteAllButton {
    color: var(--sys-red, #D93D3E);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

/* 알림 항목 기본 스타일 */
.notificationItemWrapper {
    margin: 0px;
    align-items: center;
    align-self: stretch;
    position: relative;
    overflow: hidden;
}

/* 안읽은 알림*/
.notificationItemWrapper.unread {
    background-color: #F0F8F8; 
    color: var(--gray-60, #191919);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

/* 읽은 알림  */
.notificationItemWrapper.read {
    background-color: #fff; /* 흰색 배경 */
    color: var(--gray-60, #191919);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

/* 알림 내용 영역 (스와이프 시 움직이는 부분) */
.notificationContent {
    display: flex;
    justify-content: space-between;
    padding: 30px 24px;
    width: 100%;
    min-width: 100%;
    
    cursor: pointer;
    background-color: inherit; /* 래퍼 배경색 상속 */
    
    position: relative;
    z-index: 2; /* 버튼 위에 위치 */
}

/* 액션 버튼 컨테이너 (숨겨진 영역) */
.notificationActions {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    display: flex;
    z-index: 1; /* 컨텐츠 뒤에 위치 */
    width: 160px; /* 액션 버튼 전체 너비 */
}

/* 액션 버튼 개별 스타일 */
.actionButton {
    flex: 1;
    border: none;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.2s;
}

/* 삭제 버튼 (빨간색/적갈색) */
.deleteButton {
    background-color: #c9564c; 
}

/* 읽음 버튼 (청록색/틸) */
.readButton {
    background-color: #4c9c9f; 
}

/* 알림 메시지 영역 */
.notificationMessage {
    flex-grow: 1;
}

/* 알림 시간 영역 */
.notificationTime {
    margin-left: 10px;
    min-width: 60px;
    text-align: right;
    color: var(--gray-30, #ACADAC);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

/* 알림이 없을 때 표시되는 영역 */
.notificationEmpty {
    text-align: center;
    margin: 250px auto;
    padding: 30px 24px;
    color: var(--gray-30, #ACADAC);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}