.iconWrapper {
  width: 80px;
  height: 80px;
  background-color: #39A2A5;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  position: absolute;
  box-shadow: 0 4px 10px rgba(0,0,0,0.15);
  z-index: 1000;
  right: 16px;
  bottom: 24px;
}