/* src/pages/qna/qnaWrite.module.css */

.bedge {
  border: none;
  font-size: 10px;
  font-weight: 400;
  border-radius: 24px;
  height: 24px;
  padding: 6px 10px;
}

.bedgeActive {
  background-color: #e0edff;
  color: #2563eb;
  border: 1px solid #3b82f6;
}

.filterRow {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  gap: 5px;
}

.title {
  font-size: 12px;
  font-weight: 600;
  color: #acadac;
}

.filterGroup {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 6px;
}

.filterGroupWrapper {
  padding: 20px 14px;

}

.selectSection {
  background-color: #f7f7f7;
  height: 40px;
}

.writeTitle {
  height: 87px;
  padding: 20px 10px;
  display: flex;
  flex-direction: column;
  gap: 7px;
  width: 100vw;
  border-top: solid 1px #e6e6e6;
  border-bottom: solid 1px #e6e6e6;
}

.qnaTitle {
  font-size: 18px;
  font-weight: 600;
  color: #acadac;
  border: none;
  width: 100%;
  outline: none;
}

.count {
  display: flex;
  flex-direction: row;
  font-size: 12px;
  font-weight: 600;
  gap: 2px;
}

.writeContent {
  width: 375px;
  height: 400px;
  padding: 14px 10px;
}

.content {
  border: none;
  width: 100%;
  height: 100%;
  outline: none;
  resize: none;
}
