/* 전체 프로필 카드 */
.userProfileContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 40px;
  background-color: #fff;
  border: none;
  margin: 0 auto; /* 중앙에 배치 */
  font-feature-settings: 'liga' off, 'clig' off;
  padding: 0;
}

/* 프로필 이미지 */
.profileIconPlaceholder {
  width: 80px; /* 이미지 크기 */
  height: 80px; /* 이미지 크기 */
  border-radius: 50%; /* 원형 */
  display: flex; 
}

/* 실제 프로필 이미지가 있을 경우 */
.profileImage {
  width: 100%;
  height: 100%;
  object-fit: cover; /* 이미지가 비율 유지하며 채우도록 */
}

.statsContainer {
  display: flex; 
  flex-direction: column;
  align-items: flex-start; /* 내부 요소들이 왼쪽 정렬되도록 */
}

.userName {
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 0px;
    color: #000;
}

.userEmail {
    color: var(--gray-40, #797A79);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-top: 5px;
    margin-bottom: 15px;
}

/* 팔로워/팔로잉 */
.Item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.statItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2px;
}

.statLabel {
    color: var(--gray-50, #474747);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.statValue {
    color: var(--gray-50, #474747);
    font-feature-settings: 'liga' off, 'clig' off;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}