
.btnI {
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 14px;
  font-weight: 600;

  width: 307px;
  height: 75px;

  padding: 27px 30px;

  cursor: pointer;
}

/* full width 옵션이 있을 경우 */
.fullWidth {
  width: 100%;
}

/* appearance 종류 */
.btnI__default {
  border: 0.5px solid #39a2a5;
  border-radius: 6px;
  background-color: #ffffff;
  color: #39a2a5;
}

.btnI__selected {
  border: none;
  border-radius: 6px;
  background-color: #39a2a5;
  color: #ffffff;
}

.btnI__round_selected {
  border: none;
  border-radius: 100px;
  background-color: #39a2a5;
  color: #ffffff;
}

.btnI__round_default {
  border: 0.5px solid #39a2a5;
  border-radius: 100px;
  background-color: #ffffff;
  color: #39a2a5;
}
