* {box-sizing: border-box;}
.personalCuration-wrapper .title-box{width: 100%; height: 89px; padding:30px 14px 20px 14px;
display: flex; flex-direction: column; gap:4px;}
.title-box h1{color: var(--gray-60, #191919);font-feature-settings: 'liga' off, 'clig' off; font-family: Inter; font-size: 18px;
font-style: normal; font-weight: 500; line-height: 20px; margin: 0px;}
.title-box p{color: var(--gray-40, #797A79); font-feature-settings: 'liga' off, 'clig' off; font-family: Inter;
font-size: 12px; font-style: normal; font-weight: 500; line-height: normal;margin: 0px;}
.curation-box{width: 100%; padding: 0px 14px; display: flex; flex-direction: column; align-items: center; gap:20px;}
.btn-box{padding:20px 10px; display: flex; justify-content: center; align-items: center;}
.more-btn{width: 355px; height: 39px;border-radius: 6px; border: 1px solid var(--gray-20, #C6C8C6);
background: var(--white, #FFF); display: flex; align-items: center; justify-content: center;}
.more-btn p{color: var(--gray-20, #C6C8C6); text-align: justify;font-feature-settings: 'liga' off, 'clig' off;
font-family: Inter; font-size: 16px; font-style: normal; font-weight: 400; line-height: normal; margin:0px;}
