.qnaTabs {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
}

.qnaTabButton {
  width: 173.5px;
  font-size: 16px;
  font-weight: 600;
  border-bottom: solid 2px #C6C8C6;
  border-top: none;
  border-left: none;
  border-right: none;
  background-color: white;
  color: #C6C8C6;
  padding-bottom: 18px;
}

.qnaTabButtonActive {
  color: #39A2A5;
  border-bottom: solid 2px #39A2A5;
}

.searchBar {
  width: 347px;
  height: 34px;
  border-radius: 6px;
  border: solid 1px #C6C8C6;
  padding: 8px 10px;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  gap: 6px;
  align-items: center;
}

.searchInput {
  border: none;
  width: 300px;
}

.searchIcon {
  margin-left: 10px;
  width: 12px;
  height: 12px;
}

.searchInput::placeholder {
  color: #C6C8C6;
  font-size: 12px;
  font-weight: 600;
}

.qnaContent {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.filterWrapper {
  display: flex;
  flex-direction: row;
  gap: 12px;
}

.filterBtn {
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
  border: none;
  background: white;
}

.filterBtnActive .dot {
  background-color: #39A2A5;
}

.filterBtnActive .text {
  color: #39A2A5;
  font-weight: 600;
}

.dot {
  width: 4px;
  height: 4px;
  border-radius: 100px;
  background-color: #ACADAC;
}

.text {
  font-size: 12px;
  font-weight: 600;
  color: #ACADAC;
}

.filterMoreBtn {
  border: none;
  background-color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 4px;
}

.filterMoreText {
  font-size: 12px;
  color: #39A2A5;
  font-weight: 600;
}

.filterRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 347px;
  padding: 10px 0;
}

.filterMoreIcon {
  width: 16px;
  height: 16px;
}

.qnaPreview {
  display: flex;
  flex-direction: column;
  width: 347px;
  height: 110px;
  padding: 14px;
  border-bottom: solid 1px #E6E6E6;
  gap: 23px;
}

.titleWrapper {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.subWrapper {
  display: flex;
  flex-direction: row;
  gap: 8px;
}

.title {
  font-size: 14px;
  font-weight: 600;
}

.subTitle {
  font-size: 12px;
  font-weight: 400;
  color: #797A79;
}

.num {
  font-size: 12px;
  color: #797A79;
}

.sub {
  display: flex;
  flex-direction: row;
  gap: 4px;
  align-items: center;
}

.iconWrapper {
  width: 80px;
  height: 80px;
  background-color: #39A2A5;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  position: absolute;
  box-shadow: 0 4px 10px rgba(0,0,0,0.15);
  z-index: 1000;
  right: 16px;
  bottom: 24px;
}
