*,
*::before,
*::after {
  box-sizing: border-box;
}

/* step1 전용 */
.step1 .formBox {
  width: 100%;
  padding: 30px 14px;
  display: flex;
  flex-direction: column;
  gap: 26px;
}

/* 공통 버튼 영역 */
.signBtnBox {
  width: 100%;
  padding: 50px 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nextBtn {
  width: 347px;
  height: 20px;
  border-radius: 6px;
  background: var(--gray-20, #c6c8c6);
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
}

.nextBtn p {
  color: var(--white, #fff);
  text-align: justify;
  font-feature-settings: "liga" off, "clig" off;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
  margin: 0;
}

/* next-btn 활성화 */
.nextBtn.isActive {
  width: 347px;
  height: 20px;
  border-radius: 6px;
  background: var(--primary-30, #39a2a5);
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
}

/* text-box 공통 */
.textBox {
  width: 100%;
  padding: 10px 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}

.textBox h1 {
  color: var(--gray-60, #191919);
  font-feature-settings: "liga" off, "clig" off;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin: 0px;
}

.textBox p {
  color: var(--gray-40, #797a79);
  font-feature-settings: "liga" off, "clig" off;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  margin: 0px;
}

/* step2 전용 */
.step2 .formBox {
  width: 100%;
  padding: 40px 14px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* step5 전용 */
.step5 .formBox {
  width: 100%;
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
  gap: 40px;
  align-items: center;
  justify-content: center;
}

.btnWrapper {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 255px;
  align-items: flex-start;
}

.explain {
  display: flex;
  gap: 6px;
}

.explain p {
  margin: 0px;
  color: var(--gray-40, #797a79);
  font-feature-settings: "liga" off, "clig" off;
  align-self: stretch;
  font-family: Inter;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

/* 비활성화(선택X) */
.cu {
  width: 100%;
  height: 75px;
  border-radius: 50px;
  border: 0.5px solid var(--primary-30, #39a2a5);
  background: var(--white, #fff);
}

.cu p {
  color: var(--primary-30, #39a2a5);
  text-align: center;
  font-feature-settings: "liga" off, "clig" off;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin: 0px;
}

/* cu 활성화 */
.cu.isActive {
  width: 100%;
  height: 75px;
  border-radius: 50px;
  border: 0.5px solid var(--white, #fff);
  background: var(--primary-30, #39a2a5);
}

/* 활성화 상태 텍스트 공통 (nextBtn / cu 둘 다 사용) */
.isActive p {
  color: var(--white, #fff);
  text-align: center;
  font-feature-settings: "liga" off, "clig" off;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin: 0px;
}

.completeBox {
  display: flex;
  gap: 22px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tBoxWrapper {
  display: flex;
  gap: 8px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tBoxWrapper h1 {
  color: var(--gray-50, #474747);
  text-align: justify;
  font-feature-settings: "liga" off, "clig" off;
  font-family: "Inter";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin: 0px;
}

.tBox {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
}

.tBoxWrapper p {
  color: var(--gray-40, #797a79);
  text-align: center;
  font-feature-settings: "liga" off, "clig" off;
  font-family: "Inter";
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin: 0px;
}

.step6Page {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.completeWrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.spinner {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 4px solid #e0f2f1;
  border-top-color: #39a2a5;
  animation: spin 0.8s linear infinite;
}
